<template>
  <div>

    <div
            :style="{display: showModal ? 'table' : 'none'}"
            class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">

          <div class="modal-body">
            <slot name="body">
              <form
                      method="post"
                      @submit="addEvent"
              >
                <input
                        id="name"
                        v-model="form.name"
                        type="text"
                        name="name"
                        placeholder="Enter title"
                        required
                >
                <input
                        id="place"
                        v-model="form.place"
                        type="text"
                        name="place"
                        placeholder="Enter place">
                <textarea
                        id="desc"
                        v-model="form.desc"
                        type="text"
                        name="desc"
                        placeholder="Enter description"></textarea>
                <div style="display: flex;">
                  <div style="margin: auto;display: flex">
                    <div @click="showModal = false" class="btn-submit"
                         style="margin-right: 15px;cursor: pointer">
                      Cancel
                    </div>
                    <input style="margin: 0"
                           type="submit"
                           value="Submit"
                           class="btn-submit"
                    >
                  </div>
                </div>
              </form>
            </slot>
          </div>
        </div>
      </div>
    </div>


    <div style="display: flex;height: 100%">
      <datepicker
              :highlighted="highlighted"
              v-model="datePick"
              :value="datePick"
              :inline="true"
              @input="selectDate"/>
      <div id="container-right">
        <a
                class="btn-calendar"
                @click="showModal = true">Add an event for {{ moment(datePick).format('MMM Do YYYY') }}</a>
        <div id="container-events">
          <div
                  v-for="(event) in calendar.getDayEvents(new Date(moment(datePick).format('MM[/]DD[/]YYYY')))"
                  :key="event.id"
                  class="single-event"
                  style="color:white">
            <span>{{ event.name }} {{ event.place ? 'at ' + event.place : '' }}</span>
            <button
                    style="float:right;background: transparent;color: white;border: 1px solid white"
                    @click="deleteEvent(event)">X
            </button>
            <hr>
            <span>{{ event.description }}</span>
          </div>
        </div>
      </div>
    </div>
    <keyboard
            :text.sync="cityName"
            :input="'inputCity'"/>
    <keyboard
            :text.sync="form.name"
            :input="'name'"/>
    <keyboard
            :text.sync="form.place"
            :input="'place'"/>
    <keyboard
            :text.sync="form.desc"
            :input="'desc'"/>
  </div>
</template>

<script>
  /* global Calendar */
  /* global Event */
  /* global CacheManager */
  // import {Calendar, Event, CacheManager} from 'reflectos-sdk'
  import Datepicker from 'vuejs-datepicker'
  import moment     from 'moment'

  export default {
    name: 'calendar',
    components: {
      Datepicker
    },
    props: {},

    data () {
      return {
        moment: moment,
        calendar: new Calendar(),
        showModal: false,
        datePick: new Date(),
        highlighted: {dates: []},
        events: [],
        cityName: '',
        form: {
          name: '',
          place: '',
          desc: '',
          date: null,
        },
        email: 'email1',
        password: 'password2',
        stateEmail: false,
        statePassword: false,
        cacheManager: new CacheManager('calendar')
      }
    },
    mounted () {
      this.cacheManager.store('test', 'test')
      this.highlighted['dates'].push(new Date(this.datePick))
      let events = this.cacheManager.get('events')

      if (events) {
        for (let i = 0; i < events.length; i++) {
          console.log(events[i])
          this.highlighted['dates'].push(new Date(events[i].date))
          let event = new Event(events[i].name, new Date(moment(events[i].date).format('MM[/]DD[/]YYYY')), events[i].place, events[i].description)
          this.calendar.addEvent(event)
        }
      }

    },
    methods: {
      addEvent (evt) {
        evt.preventDefault()
        let event = new Event(this.form.name, new Date(moment(this.datePick).format('MM[/]DD[/]YYYY')), this.form.place, this.form.desc)
        this.calendar.addEvent(event)

        let d = event.getDate()
        let stored = []
        let caches = this.cacheManager.get('events')
        if (caches != null) {
          for (let i = 0; i < caches.length; i++) {
            stored.push(caches[i])
          }
        }

        console.log(stored)

        stored.push(event)
        this.cacheManager.store('events', stored)

        console.log(this.cacheManager.get('events'))

        this.highlighted['dates'].push(new Date(this.datePick))
        this.form.name = ''
        this.form.place = ''
        this.form.desc = ''
        this.showModal = false
      },
      selectDate () {
        let d = new Date(moment(this.datePick).format('MM[/]DD[/]YYYY'))

        this.events = this.calendar.getDayEvents(d)
      },
      deleteEvent (event) {

        let caches = this.cacheManager.get('events')
        if (caches != null) {
          for (let i = 0; i < caches.length; i++) {
            if (event.name === caches[i].name && event.description === caches[i].description) {
              caches.splice(i, 1)
            }
          }
        }

        this.cacheManager.store('events', caches)
        this.calendar.removeEvent(event)
      }
    },
  }
</script>

